import http from './service'

// 获取新闻列表
export function getArticleList(data) {
    let url = '/api/Article'

    return http.get(url, data)
}

// 获取某一条新闻的详情
export function getArticleDetail(id) {
    let url = `/api/Article/${id}`

    return http.get(url)
}

// 获取系统基本信息
export function getSystemInfo(data) {
    let url = '/api/SystemSetting'

    return http.get(url, data)
}


