<template>
  <div>
    <scroll class="wrapper">
      <div>
        <div class="header">
          <div class="background">
            <img src="~common/image/index/background.png" />
          </div>
          <div class="logo">
            <img src="~common/image/logo.png" />
          </div>
          <div class="catchword">
            <span>{{systemInfo.merchantAppAnnouncement}}</span>
            <!-- <span>价值共生</span> -->
          </div>
          <div class="contact">
            <a class="mobile" :href="systemInfo.contactsNumber | formatTel">{{systemInfo.contactsNumber}}</a>
            <span class="seperate">|</span>
            <span class="link">{{systemInfo.website}}</span>
          </div>
          <div class="address">
            {{systemInfo.companyAddress}}
          </div>
        </div>
        <div class="news-list">
          <ul>
            <li class="item" v-for="item in list" @click="selectItem(item.id)">
              <div class="icon">
                <img :src="item.picUrl" />
              </div>
              <div class="detail">
                <h2 class="title">{{item.title}}</h2>
                <p class="time">{{item.creationTime}}</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import {setLoginPage} from 'common/js/mixin'
  import {ERR_CODE} from 'api/config'
  import {getArticleList,getSystemInfo} from 'api/index'

  export default {
    mixins: [setLoginPage],
    data () {
      return {
        list: [],
        systemInfo: {
          announcement: '',
          companyAddress: '',
          contactsNumber: '',
          systemName: '',
          website: ''
        }
      }
    },
    computed: {
      currentRoute(){
        return this.$router.currentRoute.path
      }
    },
    filters: {
      formatTel(value){
        return `tel:${value}`
      }
    },
    created () {
      this._getSystemInfo()
      this._getArticleList()
    },
    methods: {
      selectItem(id){
        this.$router.push({
          path: `${this.currentRoute}/${id}`
        })
      },
      _getSystemInfo() {
        getSystemInfo().then((res) => {
          if(res.errCode === ERR_CODE){
            this.systemInfo = res.data
          }
        })
      },
      _getArticleList() {
        getArticleList().then((res) => {
          if(res.errCode === ERR_CODE){
            this.list = res.data
          }
        })
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .wrapper
    position absolute
    left 0
    right 0
    top 0
    bottom 50px
    .header
      width 100%
      height 250px
      display flex
      flex-direction column
      align-items center
      position relative
      .background
        width 100%
        height 100%
        position absolute
        z-index -1
        img
          display block
          width 100%
          height 100%
      .logo
        width 96px
        height 89px
        margin-top 48px
        img
          display block
          width 100%
          height 100%
      .catchword
        color #fff
        font-size $font-size-large-m
        display flex
        justify-content space-around
        width 185px
        height 24px
        align-items center
        margin-top 14px
        text-align center
      .contact
        font-size $font-size-small-x
        display flex
        justify-content space-around
        align-items center
        color #fff
        width 200px
        height 18px
        margin-top 12px
        margin-bottom 10px
        a
          color #fff
        .seperate
          margin-left 5px
          margin-right 5px
      .address
        font-size $font-size-small-x
        display flex
        justify-content center
        align-items center
        color #fff
        width 100%
        height 18px
    .news-list
      margin 15px 15px 0 15px
      .item
        height 110px
        width 100%
        display flex
        background #fff
        margin-bottom 15px
        border-radius 3px
        box-shadow 0 4px 15px 0 rgba(219, 219, 219, 0.4)
        .icon
          width 110px
          height 110px
          img
            display block
            width 100%
            height 100%
      .detail
        display flex
        flex-direction column
        flex 1
        margin 14px
        justify-content space-between
        .title
          font-size $font-size-medium-m
          color #3d3d3d
          line-height 24px
          font-weight bold
        .time
          font-size $font-size-small
          color #808080

</style>



